import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Icon, NavBar } from 'antd-mobile';
import Content from '../../views/common/Content';

class FamousPersonDetailScreen extends Component
{
    render(){
        let listViewHeight = window.innerHeight;
        let famous_head = require('../../assets/images/tour/famous_person.png');
        return(
            <div className="famousPerson">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}
                    rightContent={this.renderRightMenu()}>
                    
                </NavBar>
                <Content style={{height: listViewHeight}}>
                    <div className="famousHead" style={{backgroundImage:`url(${famous_head})`}}>
                    </div>
                    <div  className="personIntro"></div>
                </Content>
            </div>
        );
    }

    renderRightMenu() {
        return (
            <img className="famousCollectIcon" onClick={this.collect} src={require('../../assets/images/tour/famous_collect.png')} alt='' />
        );
    }

}

FamousPersonDetailScreen = connect()(FamousPersonDetailScreen);

export default FamousPersonDetailScreen;


